<template>
  <div class="home">
    <div class="inputArea">
      <div class="item">
        <div class="title"></div>
        <el-input
          v-model="hedgePro.currencys"
          placeholder=""
        ></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="hedgePro.period" placeholder=""></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="hedgePro.size" placeholder=""></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-date-picker
          v-model="hedgePro.dates"
          type="datetimerange"
          align="right"
          start-placeholder=""
          end-placeholder=""
          :default-time="['00:00:00', '00:00:00']"
          @change="getData3"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </div>
      <div class="item">
        <div class="title">ws</div>
        <el-input
          v-model="hedgePro.timeGap"
          placeholder=""
        ></el-input>
      </div>
      <div
        class="item"
        v-for="(item, index) in riseShowData"
        :key="index + Math.random()"
      >
        <div>{{ item.period }}</div>
        <div style="margin-left: 10px">
          {{ item.currency0 + "-" + item.currency1 }}
        </div>
        <div style="margin-left: 10px">
          {{ item.tick0.close + "-" + item.tick1.close }}
        </div>
        <div style="margin-left: 10px">
          {{ item.rise0 + "-" + item.rise1 }}
        </div>
        <div style="margin-left: 10px">
          {{ item.rise }}
        </div>
      </div>
    </div>
    <div class="buttons">
      <el-button
        type="primary"
        :loading="loading3"
        :disabled="disabled3"
        @click="daydayday(1)"
        ></el-button
      >
      <el-button
        type="primary"
        :loading="loading3"
        @click="getData3()"
        :disabled="disabled3"
        ></el-button
      >
      <el-button
        type="primary"
        :loading="loading3"
        :disabled="disabled3"
        @click="daydayday(2)"
        ></el-button
      >
      <el-button type="danger" @click="klineData = []"></el-button>
      <el-button
        type="primary"
        :loading="loading3"
        @click="calculateDouble1(klineData)"
        :disabled="disabled3"
        ></el-button
      >
    </div>
    <el-divider></el-divider>

    <div class="data">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
      >
        <el-table-column prop="currency" label="currency"></el-table-column>
        <el-table-column prop="length" label="length"> </el-table-column>

        <el-table-column
          prop="start"
          label="start"
          width="165"
        ></el-table-column>
        <el-table-column prop="end" label="end" width="165"> </el-table-column>

        <el-table-column fixed="right" label="" width="80">
          <template slot-scope="scope">
            <el-button
              type="danger"
              size="small"
              @click="klineData.splice(scope.$index, 1)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-divider></el-divider>
    <div id="main" style="height: 500px"></div>
    <el-divider></el-divider>
    <div v-for="(item, index) in ccData" :key="index">
      <div :id="'main' + index" style="height: 500px"></div>
      <el-divider></el-divider>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
var axios = require("axios");
import HelloWorld from "@/components/HelloWorld.vue";
import { mapState, mapMutations, mapGetters } from "vuex";
import CryptoJS from "crypto-js/crypto-js";
import * as echarts from "echarts";
export default {
  // name: "Home",
  components: {
    HelloWorld,
  },
  data() {
    return {
      input: "",
      loading: false,
      loading2: false,
      loading3: false,
      disabled3: true,
      ws: null,
      ccData: [],
      riseData: {},
      riseShowData: [],
      myChart: null,
      klineData: [],
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              let m = 24 * 60 * 60 * 1000;
              let time = new Date().getTime();
              const start = time - (time % m) + 16 * 60 * 60 * 1000;
              const end = start + m;
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      temKlineData: [],
      loadingTag: "",
    };
  },
  computed: {
    ...mapState({
      hedgePro: (state) => state.hedgePro,
      urls: (state) => state.urls,
      calculateData: (state) => state.calculateData,
      spotData: (state) =>
        state.accounts.filter((item) => item.type == "spot")[0] || {},
    }),
    currencys() {
      return this.hedgePro.currencys.split(",");
    },
    periods() {
      return this.hedgePro.period.split(",");
    },
    tableData() {
      return this.klineData.map((item) => {
        return {
          currency: item.currency0 + "," + item.currency1,
          length: item.list0.length,
          start: _D(item.list0.length ? item.list0[0].id * 1000 : 0),
          end: _D(
            item.list1.length ? item.list1[item.list1.length - 1].id * 1000 : 0
          ),
          time: _D(),
        };
      });
    },
    option() {
      let item0 = this.ccData[0];
      return {
        roam: false,
        title: {
          text: "",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          data: this.ccData.map(
            (item) => item.currency0 + "-" + item.currency1
          ),
        },
        toolbox: {
          feature: {
            saveAsImage: {},
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: this.ccData.map((item) => {
          return {
            type: "category",
            boundaryGap: false,
            data: item.rises.map((item) => item.time),
          };
        }),
        yAxis: [
          {
            type: "value",
          },
        ],
        series: this.ccData.map((item) => {
          return {
            name: item.currency0 + "-" + item.currency1,
            type: "line",
            // stack: "",
            areaStyle: {},
            emphasis: {
              focus: "series",
            },
            data: item.rises.map((item) => item.rise),
          };
        }),
        dataZoom: [
          {
            show: true,
            type: "inside",
            realtime: true,
            start: 0,
            end: 100,
          },
          {
            show: true,
            type: "slider",
            realtime: true,
            start: 0,
            end: 100,
          },
        ],
      };
    },
  },
  created() {
    this.initWS();
  },
  mounted() {
    // var myChart = echarts.init(document.getElementById("main"));
    // this.myChart = myChart;
    // myChart.setOption(this.option);
  },
  destroyed() {
    this.ws.close();
  },
  methods: {
    getWSData1(data) {
      if (data && data.status == "ok") {
        data = data.data;
        Log(
          "data.length",
          data.length,
          _D(this.loadingTag.from),
          _D(this.loadingTag.to)
        );
        if (data.length) {
          this.temKlineData = this.temKlineData.concat(data);
          setTimeout(() => {
            this.queryKLineNext();
          }, this.hedgePro.timeGap);
        } else {
          this.loadingTag = null;
        }
      } else {
        // Log("getWSData1", JSON.stringify(data));
      }
    },
    queryKLineNext() {
      let currency = this.loadingTag.currency;
      let period = this.loadingTag.period;
      let size = this.loadingTag.size;
      let from = this.loadingTag.to.getTime() + this.getTimeGap(period, size);
      this.queryKLine3(currency, period, from);
    },
    queryKLineFromWS(currency, period, from = this.hedgePro.dates[0]) {
      this.temKlineData = [];
      return this.queryKLine3(currency, period, from);
    },
    queryKLine3(currency, period, from) {
      let to = new Date(
        Math.min(
          new Date(from).getTime() +
            this.getTimeGap(period) * this.hedgePro.size -
            60 * 1000,
          new Date(this.hedgePro.dates[1])
        )
      );
      if (new Date(from) < new Date(to)) {
        let loadingTag = {
          currency,
          period,
          from,
          to,
          size: this.hedgePro.size,
          tag: new Date().getTime(),
        };
        this.loadingTag = loadingTag;
        this.queryKLine4(loadingTag);
      } else {
        this.loadingTag = null;
      }
      return new Promise((reslove, reject) => {
        // setTimeout(() => {
        //   this.loadingTag = null
        // }, 30000);
        this.superRunLoop(reslove);
      });
    },

    queryKLine4(loadingTag) {
      let currency = loadingTag.currency;
      let period = loadingTag.period;
      let to = loadingTag.to;
      let from = loadingTag.from;
      let id = loadingTag.tag;
      let data = {
        req: `market.${currency}usdt.kline.${period}`,
        to: _N(new Date(to) / 1000),
        from: _N(new Date(from) / 1000),
        id,
      };

      this.ws.send(JSON.stringify(data));
    },

    superRunLoop(reslove) {
      if (!this.loadingTag) {
        // this.temKlineData.splice(this.temKlineData.length - 1, 1)
        reslove(this.temKlineData);
      } else {
        setTimeout(() => {
          this.superRunLoop(reslove);
        }, 10);
      }
    },
    daydayday(type) {
      let m = 24 * 60 * 60 * 1000 * (type == 1 ? -1 : 1);
      let dates = this.hedgePro.dates;
      this.hedgePro.dates = [
        new Date(new Date(dates[0]).getTime() + m),
        new Date(new Date(dates[1]).getTime() + m),
      ];
      this.getData3();
    },
    getData4() {},
    async getData3() {
      this.loading3 = true;
      this.klineData = [];
      let currencys = this.currencys;

      let arr = [];
      for (let index = 0; index < currencys.length; index++) {
        const currency = currencys[index];
        let currency0, currency1;
        if (currency[currency.length - 1] > "9") {
          currency0 = currency;
          currency1 = currency + "1s";
        } else {
          currency0 = currency + "l";
          currency1 = currency + "s";
        }
        let list0 = await this.queryKLineFromWS(
          currency0,
          this.hedgePro.period
        );
        let list1 = await this.queryKLineFromWS(
          currency1,
          this.hedgePro.period
        );
        arr.push({
          currency0,
          currency1,
          list0,
          list1,
        });
      }
      this.klineData = arr;
      this.loading3 = false;
      this.calculateDouble1(this.klineData);
      return arr;
    },
    getTimeGap(period) {
      let scale = 1;
      if (period.includes("hour")) {
        scale = 60;
      } else if (period.includes("day")) {
        scale = 60 * 24;
      } else if (period.includes("mon")) {
        scale = 60 * 24 * 30;
      }
      let m = parseInt(period) * scale;
      let gapM = m * 60 * 1000;
      return gapM;
    },
    async calculateDouble1(klineData) {
      let arr = klineData.map((item) => this.calculateData2(item));
      Log("", arr.length);
      this.loading2 = false;
      this.ccData = arr;
      var myChart = echarts.init(document.getElementById("main"));
      this.myChart = myChart;
      this.myChart.setOption(this.option);
      setTimeout(() => {
        this.renderCharts();
      }, 100);
    },
    renderCharts() {
      this.ccData.forEach((item, index) => {
        var myChart = echarts.init(document.getElementById("main" + index));
        let legendData = ["rise0", "rise1", "rise"];
        let xAxisData = [item.rises.map((item) => item.time)];
        let seriesData = [
          item.rises.map((item) => item.rise0),
          item.rises.map((item) => item.rise1),
          item.rises.map((item) => item.rise),
        ];
        let option = this.getOption(
          item.currency0 + "-" + item.currency1,
          legendData,
          xAxisData,
          legendData,
          seriesData
        );
        myChart.setOption(option);
      });
    },
    getOption(titleText, legendData, xAxisData, seriesName, seriesData) {
      return {
        title: {
          text: titleText,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          data: legendData,
        },
        toolbox: {
          feature: {
            saveAsImage: {},
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: xAxisData.map((item) => {
          return {
            type: "category",
            boundaryGap: false,
            data: item.map((item) => item),
          };
        }),
        yAxis: [
          {
            type: "value",
          },
        ],
        series: seriesData.map((item, index) => {
          return {
            name: seriesName[index],
            type: "line",
            // stack: "",
            areaStyle: {},
            emphasis: {
              focus: "series",
            },
            data: item.map((item) => item),
          };
        }),
        dataZoom: [
          {
            show: true,
            type: "inside",
            realtime: true,
            start: 0,
            end: 100,
          },
          {
            show: true,
            type: "slider",
            realtime: true,
            start: 0,
            end: 100,
          },
        ],
      };
    },
    calculateData2(klineData) {
      let list0 = klineData.list0;
      let list1 = klineData.list1;
      let rises = [];
      list0.some((item, index) => {
        let item0 = list0[index];
        let item1 = list1[index];
        let rise0 = (item0.close - list0[0].open) / list0[0].open;
        let rise1 = (item1.close - list1[0].open) / list1[0].open;
        let rise = rise0 + rise1;
        rises.push({
          time: _D(item.id * 1000),
          rise,
          rise0,
          rise1,
        });
      });
      let item = {
        currency0: klineData.currency0,
        currency1: klineData.currency1,
        rises,
      };
      return item;
    },
    initWS() {
      let ws = new WebSocket("wss://api.huobi.pro/ws");
      this.ws = ws;
      ws.onopen = (evt) => {
        //
        Log("Connection open ...");
        this.disabled3 = false;
      };
      ws.onmessage = (evt) => {
        //
        // console.log("Received Message: " + evt.data);
        var content = evt.data; //
        var reader = new FileReader();
        reader.readAsArrayBuffer(content);
        let that = this;
        reader.onload = () => {
          content = reader.result;
          const pako = require("pako");
          const restored = pako.inflate(content);
          let str = Uint8ArrayToString(restored);
          // Log("WS Received Message: " + str);
          let data = JSON.parse(str);
          if (data.ping) {
            let pong = JSON.stringify({
              pong: data.ping,
            });
            ws.send(pong);
          } else {
            if ((data.id = this.loadingTag)) {
              this.getWSData1(data);
            } else {
              Log("ext data", data);
            }
          }
          if (data.status == "error") {
            this.$message.error(data["err-msg"]);
            this.loading3 = false;
          }
        };
      };

      ws.onclose = (evt) => {
        //
        Log("Connection closed.");
        this.disabled3 = true;
        this.loadingTag = null;
        setTimeout(() => {
          this.initWS();
        }, 1000);
      };
    },
  },
};

function _D(time) {
  return require("dayjs")(time).format("YYYY-MM-DD HH:mm:ss");
}

function _N(time, num = 0) {
  return Math.floor(time * Math.pow(10, num)) / Math.pow(10, num);
}

function _Round(time, num = 0) {
  return Math.round(time * Math.pow(10, num)) / Math.pow(10, num);
}

function Log() {
  console.log(_D(), ...arguments);
}
function Uint8ArrayToString(fileData) {
  var dataString = "";
  for (var i = 0; i < fileData.length; i++) {
    dataString += String.fromCharCode(fileData[i]);
  }
  return dataString;
}
</script>


