<template>
  <div class="home">
    <div class="inputArea">
      <div class="item">
        <div class="title"></div>
        <el-input
          v-model="config1.currencys"
          placeholder=""
        ></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="config1.period" placeholder=""></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="config1.size" placeholder=""></el-input>
      </div>
    </div>
    <div class="buttons">
      <el-button type="primary" :loading="loading" @click="getData"
        ></el-button
      >
      <el-button type="primary" @click="testClick()">{{
        spotData.id || "ID"
      }}</el-button>
      <el-button type="primary" @click="testClick2()">2</el-button>
      <el-button type="danger" @click="$store.state.klineData = []"
        ></el-button
      >
    </div>
    <div class="data">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
      >
        <el-table-column
          prop="currency"
          label="currency"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="period"
          label="period"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="price"
          label="price"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="start"
          label="start"
          width="165"
        ></el-table-column>
        <el-table-column prop="end" label="end" width="165"> </el-table-column>
        <el-table-column prop="time" label="time" width="165">
        </el-table-column>

        <el-table-column prop="length" label="length"> </el-table-column>
        <el-table-column fixed="right" label="" width="150">
          <template slot-scope="scope">
            <el-button
              @click="handleClick(scope, 1)"
              type="primary"
              size="small"
              ></el-button
            >
            <el-button
              type="danger"
              size="small"
              @click="$store.state.klineData.splice(scope.$index, 1)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-divider></el-divider>
    <div class="inputArea">
      <div class="item">
        <div class="title"></div>
        <el-input
          v-model="config1.assetsSplit"
          placeholder=""
        ></el-input>
      </div>
      <div class="item">
        <div class="title">U</div>
        <el-input
          v-model="config1.assetsAll"
          placeholder=""
        ></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="config1.riseKey" placeholder=""></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="config1.gas" placeholder=""></el-input>
      </div>
    </div>
    <div class="buttons">
      <el-button
        type="primary"
        :loading="loading2"
        @click="calculate(klineData)"
        ></el-button
      >
      <el-button type="danger" @click="$store.state.calculateData = []"
        ></el-button
      >
    </div>
    <div class="data">
      <el-table
        :data="calculateData"
        border
        style="width: 100%"
        v-loading="loading2"
      >
        <el-table-column prop="currency" label="currency"></el-table-column>
        <el-table-column prop="period" label="period"></el-table-column>
        <el-table-column prop="income" label="income" sortable width="200">
        </el-table-column>
        <el-table-column prop="fprice" label="fprice"></el-table-column>

        <el-table-column prop="price" label="price"></el-table-column>
        <el-table-column prop="length" label="length"> </el-table-column>
        <el-table-column prop="assetsAll" label="U"> </el-table-column>
        <el-table-column prop="numAll" label=""> </el-table-column>

        <el-table-column prop="assets" label="assets"> </el-table-column>

        <el-table-column prop="num" label="num"> </el-table-column>

        <el-table-column prop="all1" label="all1"> </el-table-column>

        <el-table-column prop="all2" label="all2"> </el-table-column>
        <el-table-column prop="all3" label="all3"> </el-table-column>

        <el-table-column prop="buyCount" label="buyCount"> </el-table-column>

        <el-table-column prop="sellCount" label="sellCount"> </el-table-column>

        <el-table-column prop="buyError" label="buyError"> </el-table-column>

        <el-table-column prop="sellError" label="sellError"> </el-table-column>

        <el-table-column
          prop="start"
          label="start"
          width="165"
        ></el-table-column>

        <el-table-column prop="end" label="end" width="165"> </el-table-column>
        <el-table-column prop="time" label="time" width="165"></el-table-column>
        <el-table-column fixed="right" label="" width="150">
          <template slot-scope="scope">
            <el-button
              @click="handleClick(scope, 2)"
              type="primary"
              size="small"
              ></el-button
            >
            <el-button
              type="danger"
              size="small"
              @click="$store.state.calculateData.splice(scope.$index, 1)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-divider></el-divider>
  </div>
</template>

<script>
// @ is an alias to /src
var axios = require("axios");
import HelloWorld from "@/components/HelloWorld.vue";
import { mapState, mapMutations, mapGetters } from "vuex";
import CryptoJS from "crypto-js/crypto-js";
export default {
  name: "Home",
  components: {
    HelloWorld,
  },
  data() {
    return {
      input: "",
      loading: false,
      loading2: false,
    };
  },
  computed: {
    ...mapState({
      config1: (state) => state.config1,
      urls: (state) => state.urls,
      klineData: (state) => state.klineData,
      calculateData: (state) => state.calculateData,
      spotData: (state) =>
        state.accounts.filter((item) => item.type == "spot")[0] || {},
    }),
    currencys() {
      return this.config1.currencys.split(",");
    },
    periods() {
      return this.config1.period.split(",");
    },
    tableData() {
      return this.klineData.map((item) => {
        return {
          currency: item.currency,
          period: item.period,
          price: item.price,
          length: item.list.length,
          start: _D(item.list[0].id * 1000),
          end: _D(item.list[item.list.length - 1].id * 1000),
          time: _D(),
        };
      });
    },
  },
  methods: {
    testClick() {
      let params = this.$util.huobiParams({}, this.urls.accounts, "get");
      console.log(params);
      return new Promise((reslove, reject) => {
        axios({
          method: "get",
          url: this.urls.accounts,
          params,
        })
          .then((res) => {
            if (res.data["err-msg"]) {
              console.log(res.data["err-msg"]);
            } else {
              this.$store.state.accounts = res.data.data;
            }
          })
          .catch((res) => {
            Log(":testCLick", res);
            reslove([]);
          });
      });
    },
    testClick2() {
      let url = `https://api.huobi.pro/v1/account/accounts/${this.spotData.id}/balance`;
      let params = this.$util.huobiParams({}, url, "get");
      console.log(params);
      return new Promise((reslove, reject) => {
        axios({
          method: "get",
          url: url,
          params,
        })
          .then((res) => {
            if (res.data["err-msg"]) {
              console.log(res.data["err-msg"]);
            } else {
              let list = res.data.data.list;
              let arr = list.filter(
                (item) =>
                  ["usdt", "btc3l", "btc3s"].includes(item.currency) &&
                  item.type == "trade"
              );
              console.log("arr", arr);
            }
          })
          .catch((res) => {
            Log(":testCLick", res);
            reslove([]);
          });
      });
    },
    handleClick(scope, type) {
      if (type == 1) {
        this.$store.state.calculateData = [
          ...this.calculate1([this.klineData[scope.$index]]),
          ...this.$store.state.calculateData,
        ];
      } else if (type == 2) {
        this.$store.state.calculateData.splice(
          scope.$index,
          1,
          this.calculate1([this.calculateData[scope.$index].item])[0]
        );
      }
    },
    getData() {
      let currencys = this.currencys;
      let periods = this.periods;
      let arr = [];
      let klineData = [];
      currencys.forEach((currency) => {
        periods.forEach((period) => {
          arr.push(
            GetRecords(currency, period, this.config1, this.urls.klineUrl)
          );
          klineData.push({
            currency,
            period,
            list: [],
            price: 0,
          });
        });
      });
      console.log(currencys);
      console.log(periods);
      this.loading = true;
      Promise.all(arr)
        .then((result) => {
          this.loading = false;
          result.forEach((element, index) => {
            klineData[index].list = [...element].reverse();
            klineData[index].price = klineData[index].list[0].close;
          });
          this.$store.state.klineData = [
            ...klineData,
            ...this.$store.state.klineData,
          ];
        })
        .catch((error) => {
          console.log(error);
        });
    },
    calculate(klineData) {
      this.loading2 = true;
      this.$store.state.calculateData = [
        ...this.calculate1(klineData),
        ...this.$store.state.calculateData,
      ];
      this.loading2 = false;
    },
    calculate1(klineData) {
      return klineData.map((item) => {
        return this.calculate2(item);
      });
    },
    calculate2(item) {
      let info = {
        buyCount: 0,
        sellCount: 0,
        buyError: 0,
        sellError: 0,
      };
      let Assets = parseFloat(this.config1.assetsAll);
      let Num = parseFloat(this.config1.assetsAll) / item.price;
      let records = item.list;
      records.some((item, index) => {
        // Log(params.symbol, index, _D(item.id * 1000))
        let rise = (item.close - item.open) / item.open;
        if (Math.abs(rise) > parseFloat(this.config1.riseKey)) {
          let num = parseFloat(this.config1.assetsSplit) / item.close;
          if (rise > 0) {
            // 
            if (Num > num) {
              Assets +=
                parseFloat(this.config1.assetsSplit) *
                (1 - parseFloat(this.config1.gas));
              Num -= num;
              info.sellCount++;
            } else {
              info.sellError++;
              // Log('')
            }
          } else {
            // 
            if (Assets > parseFloat(this.config1.assetsSplit)) {
              Assets -= parseFloat(this.config1.assetsSplit);
              Num += num * (1 - parseFloat(this.config1.gas));
              info.buyCount++;
            } else {
              info.buyError++;
              // Log('')
            }
          }
          // if (isNaN(Num)) {
          //   debugger;
          // }
        }
      });
      let data = {
        currency: item.currency,
        period: item.period,
        fprice: item.list[0].close,
        price: item.list[item.list.length - 1].close,
        length: item.list.length,
        all1:
          parseFloat(this.config1.assetsAll) +
          (parseFloat(this.config1.assetsAll) / item.price) * records[0].close,
        all2:
          parseFloat(this.config1.assetsAll) +
          (parseFloat(this.config1.assetsAll) / item.price) *
            records[records.length - 1].close,

        all3: Assets + Num * records[records.length - 1].close,
        start: _D(item.list[0].id * 1000),
        end: _D(item.list[item.list.length - 1].id * 1000),
        time: _D(),
        assetsAll: this.config1.assetsAll,
        numAll: parseFloat(this.config1.assetsAll) / item.price,
        assets: Assets,
        num: Num,
        ...info,
        item,
      };
      data.income = data.all3 - data.all1;
      return data;
    },
  },
};

function GetRecords(currency, period, config1, url) {
  let size = config1.size;
  let params = {
    symbol: (currency + "usdt").toLowerCase(),
    period,
    size,
  };
  return new Promise((reslove, reject) => {
    axios({
      method: "get",
      url,
      params,
    })
      .then((res) => {
        let records = res.data.data;
        reslove(records);
      })
      .catch((res) => {
        Log(":GetRecords", res);
        reslove([]);
      });
  });
}
function _D(time) {
  return require("dayjs")(time).format("YYYY-MM-DD HH:mm:ss");
}

function _N(time, num = 0) {
  return Math.floor(time * Math.pow(10, num)) / Math.pow(10, num);
}
function Log() {
  console.log(_D(), ...arguments);
}
</script>


