        ></el-input>
      </div>

      <div class="item">
        <div class="title"></div>
        <el-input v-model="config2.riseKey" placeholder=""></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="config2.gas" placeholder=""></el-input>
