      <el-button type="danger" @click="$store.state.ccData = []"
        ></el-button
      >
    </div>
    <div class="data">
      <el-table :data="ccData" border style="width: 100%" v-loading="loading2">
        <el-table-column prop="time" label="time" width="165"></el-table-column>

        <el-table-column
          prop="currency"
          label="currency"
          width="100"
        ></el-table-column>
        <el-table-column prop="period" label="period"></el-table-column>
        <el-table-column prop="assetsAll" label=""> </el-table-column>

        <el-table-column prop="change" label="change" sortable>
        </el-table-column>
        <el-table-column prop="rate" label="rate" sortable> </el-table-column>

        <el-table-column prop="assetsSplit" label="assetsSplit">
        </el-table-column>
        <el-table-column prop="assetsScale" label="assetsScale">
        </el-table-column>

        <el-table-column prop="riseKey" label="riseKey"> </el-table-column>

        <el-table-column prop="assets" label="assets"> </el-table-column>
        <el-table-column prop="assets0" label="assets0"> </el-table-column>

        <el-table-column prop="assets1" label="assets1"> </el-table-column>
        <el-table-column prop="assetsNum0" label="assetsNum0">
        </el-table-column>

        <el-table-column prop="assetsNum1" label="assetsNum1">
        </el-table-column>

        <el-table-column prop="length" label="length"> </el-table-column>

        <el-table-column prop="buyCount" label="buyCount"> </el-table-column>

        <el-table-column prop="sellCount" label="sellCount"> </el-table-column>

        <el-table-column prop="buyError" label="buyError"> </el-table-column>

        <el-table-column prop="sellError" label="sellError"> </el-table-column>

        <el-table-column prop="gas" label="gas"> </el-table-column>

        <el-table-column fixed="right" label="" width="80">
          <template slot-scope="scope">
