<script>
// @ is an alias to /src
var axios = require("axios");
import HelloWorld from "@/components/HelloWorld.vue";
import { mapState, mapMutations, mapGetters } from "vuex";
import CryptoJS from "crypto-js/crypto-js";
export default {
  // name: "Home",
  components: {
    HelloWorld,
  },
  data() {
    return {
      input: "",
      loading: false,
      loading2: false,
      loading3: false,
      disabled3: true,
      ws: null,
    };
  },
  computed: {
    ...mapState({
      config2: (state) => state.config2,
      urls: (state) => state.urls,
      klineData: (state) => state.klineData,
      calculateData: (state) => state.calculateData,
      ccData: (state) => state.ccData,
      spotData: (state) =>
        state.accounts.filter((item) => item.type == "spot")[0] || {},
    }),
    currencys() {
      return this.config2.currencys.split(",");
    },
    periods() {
      return this.config2.period.split(",");
    },
    tableData() {
      return this.klineData.map((item) => {
        return {
          currency: item.currency,
          period: item.period,
          price: item.price,
          length: item.list.length,
          start: _D(item.list.length ? item.list[0].id * 1000 : 0),
          end: _D(
            item.list.length ? item.list[item.list.length - 1].id * 1000 : 0
          ),
          time: _D(),
        };
      });
    },
  },
  created() {
    let ws = new WebSocket("wss://api.huobi.pro/ws");
    this.ws = ws;
    ws.onopen = (evt) => {
      //
      Log("Connection open ...");
      this.disabled3 = false;
    };
    ws.onmessage = (evt) => {
      //
      // console.log("Received Message: " + evt.data);
      var content = evt.data; //
      var reader = new FileReader();
      reader.readAsArrayBuffer(content);
      let that = this;
      reader.onload = () => {
        content = reader.result;
        const pako = require("pako");
        const restored = pako.inflate(content);
        let str = Uint8ArrayToString(restored);
        // Log("WS Received Message: " + str);
        let data = JSON.parse(str);
        if (data.ping) {
          let pong = JSON.stringify({
            pong: data.ping,
          });
          ws.send(pong);
        } else {
          this.getWSData(data);
        }
        if (data.status == "error") {
          this.$message.error(data["err-msg"]);
          this.loading3 = false;
        }
      };
    };

    ws.onclose = (evt) => {
      //
      Log("Connection closed.");
      this.disabled3 = true;
    };
  },
  destroyed() {
    this.ws.close();
  },
  methods: {
    getWSData(data) {
      if (data && data.status == "ok") {
        data = data.data;
        if (this.wsParams.index < this.klineData.length) {
          this.klineData[this.wsParams.index].list = [
            ...this.klineData[this.wsParams.index].list,
            ...data,
          ];
          this.wsParams.arr[this.wsParams.index].index++;
        } else {
          let item = this.wsParams.arr[this.wsParams.index];
          this.klineData.push({
            currency: item.currency,
            period: item.period,
            list: data,
            price: "-",
          });
          this.wsParams.arr[this.wsParams.index].index++;
        }
        let item = this.wsParams.arr[this.wsParams.index];
        if (item.index >= item.list.length) {
          this.wsParams.index++;
        }
        if (
          this.wsParams.index >= this.klineData.length &&
          item.index >= item.length
        ) {
          this.loading3 = false;
          return;
        }
        setTimeout(() => {
          this.queryKLineFromWS();
        }, this.config2.timeGap);
      } else {
        Log("getWSData", JSON.stringify(data));
      }
    },
    queryKLineFromWS() {
      // this.wsParams
      if (this.wsParams.index < this.wsParams.arr.length) {
        let item = this.wsParams.arr[this.wsParams.index];
        let currency = item.currency;
        let period = item.period;
        let item2 = item.list[item.index];
        let from = item2.from;
        let to = item2.to;
        Log(currency, period, _D(from * 1000), _D(to * 1000));
        this.ws.send(
          JSON.stringify({
            req: `market.${currency}usdt.kline.${period}`,
            to,
            from,
          })
        );
        return;
      } else {
        Log("queryKLineFromWS end");
        this.loading3 = false;
      }
    },
    testClick(klineData) {
      if (klineData.length == 2) {
        let list0 = klineData[0].list;
        let list1 = klineData[1].list;
        for (const key in list0) {
          if (list0[key].id == list1[key].id) {
          } else {
            this.$message.error("" + key);
            return;
          }
        }
        this.loading2 = true;
        let data = [
          this.config2.assetsAll,
          this.config2.assetsSplit,
          this.config2.assetsScale,
          this.config2.riseKey,
          this.config2.gas,
        ].map((item) => {
          let list = [];
          let arr = String(item).split("-");
          if (arr.length > 1) {
            let arr2 = arr[1].split(".");
            let gap = 1;
            if (arr2.length > 1) {
              gap = Math.pow(10, -arr2[1].length);
            }
            let start = parseFloat(arr[0]);
            let newNum = start;
            list.push(start);
            while (parseFloat(list[list.length - 1]) < parseFloat(arr[1])) {
              newNum = _Round(
                list[list.length - 1] + gap,
                gap == 1 ? 0 : arr2[1].length
              );
              list.push(newNum);
            }
          } else {
            list = arr;
          }
          return list;
        });

        var worker = new Worker("./work.js");
        worker.postMessage({
          data,
          klineData,
        });
        worker.onmessage = (event) => {
          // console.log("Received message " + event.data);
          this.loading2 = false;

          let arr = event.data;

          Log("", arr.length);
          this.$store.state.ccData = arr;
          worker.terminate();
        };
      } else {
        this.$message.error("");
      }
    },
    testClick2() {},
    handleClick(scope, type) {
      if (type == 1) {
        this.$store.state.calculateData = [
          ...this.calculate1([this.klineData[scope.$index]]),
          ...this.$store.state.calculateData,
        ];
      } else if (type == 2) {
        this.$store.state.calculateData.splice(
          scope.$index,
          1,
          this.calculate1([this.calculateData[scope.$index].item])[0]
        );
      }
    },
    getData3() {
      this.loading3 = true;
      this.$store.state.klineData = [];
      let currencys = this.currencys;
      let periods = this.periods;
      let size = parseInt(this.config2.size);
      let date0 = new Date(this.config2.dates[0]);
      let date1 = new Date(this.config2.dates[1]);
      let arr = [];
      currencys.forEach((currency) => {
        periods.forEach((period) => {
          let m = parseInt(period);
          let gapM = m * size * 60;
          let time = date0.getTime() / 1000;
          let list = [];
          while (time < date1.getTime() / 1000) {
            let item = {
              from: time,
              to:
                time + gapM > date1.getTime() / 1000
                  ? date1.getTime() / 1000
                  : time + gapM,
            };
            item.fromT = _D(item.from * 1000);
            item.toT = _D(item.to * 1000);

            list.push(item);
            time += gapM + m * 60;
          }
          arr.push({
            currency,
            period,
            list,
            index: 0,
          });
        });
      });
      this.wsParams = {
        arr,
        index: 0,
      };
      this.queryKLineFromWS();
    },
    getData(type) {
      let currencys = this.currencys;
      let periods = this.periods;
      let arr = [];
      let klineData = [];
      currencys.forEach((currency) => {
        periods.forEach((period) => {
          arr.push(
            type == 1
              ? GetRecords(currency, period, this.config2, this.urls.klineUrl)
              : GetRecords2(
                  currency,
                  period,
                  this.config2,
                  "http://localhost:8888/",
                  this.config2.dates
                )
          );
          klineData.push({
            currency,
            period,
            list: [],
            price: 0,
          });
        });
      });
      console.log(currencys);
      console.log(periods);
      this.loading = true;
      Promise.all(arr)
        .then((result) => {
          this.loading = false;
          result.forEach((element, index) => {
            klineData[index].list = [...element].reverse();
            klineData[index].price = klineData[index].list.length
              ? klineData[index].list[0].close
              : 0;
          });
          this.$store.state.klineData = [...klineData];

          let list0 = this.$store.state.klineData[0].list;
          let list1 = this.$store.state.klineData[1].list;

          if (list0[0].id < list1[0].id) {
            list0.some((item, index) => {
              if (item.id == list1[0].id) {
                list0.splice(0, index);
              }
            });
          }
          if (list0[0].id > list1[0].id) {
            list1.some((item, index) => {
              if (item.id == list0[0].id) {
                list1.splice(0, index);
              }
            });
          }
          if (list0.length > list1.length) {
            list0.splice(list1.length, list0.length - list1.length);
          }
          if (list1.length > list0.length) {
            list1.splice(list0.length, list1.length - list0.length);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async calculateDouble(klineData) {
      if (klineData.length == 2) {
        let list0 = klineData[0].list;
        let list1 = klineData[1].list;

        list0.some((item, key) => {
          if (list0[key].id == list1[key].id) {
          } else {
            this.$message.error("" + key);
            return true;
          }
        });
        this.loading2 = true;

        let data = [
          this.config2.assetsAll,
          this.config2.assetsSplit,
          this.config2.assetsScale,
          this.config2.riseKey,
          this.config2.gas,
        ].map((item) => {
          let list = [];
          let arr = String(item).split("-");
          if (arr.length > 1) {
            let arr2 = arr[1].split(".");
            let gap = 1;
            if (arr2.length > 1) {
              gap = Math.pow(10, -arr2[1].length);
            }
            let start = parseFloat(arr[0]);
            let newNum = start;
            list.push(start);
            while (parseFloat(list[list.length - 1]) < parseFloat(arr[1])) {
              newNum = _Round(
                list[list.length - 1] + gap,
                gap == 1 ? 0 : arr2[1].length
              );
              list.push(newNum);
            }
          } else {
            list = arr;
          }
          return list;
        });

        let list = [];
        data[0].forEach((item0) => {
          data[1].forEach((item1) => {
            data[2].forEach((item2) => {
              data[3].forEach((item3) => {
                data[4].forEach((item4) => {
                  list.push([item0, item1, item2, item3, item4]);
                });
              });
            });
          });
        });
        let arr = [];
        list.forEach((item) => {
          arr.push(
            this.calculateDouble2(
              klineData,
              item[0],
              item[1],
              item[2],
              item[3],
              item[4]
            )
          );
        });
        Log("", arr.length);
        this.loading2 = false;
        this.$store.state.ccData = arr.filter(
          (item) => item.rate > parseFloat(this.config2.filterRise)
        );
      } else {
        this.$message.error("");
      }
    },
    calculateDouble2(
      klineData,
      assetsAll = 100,
      assetsSplit = 5,
      assetsScale = 1.2,
      riseKey = 0.005,
      gas = 0.002
    ) {
      assetsSplit = parseFloat(assetsSplit);
      assetsScale = parseFloat(assetsScale);
      riseKey = parseFloat(riseKey);
      gas = parseFloat(gas);

      let info = {
        buyCount: 0,
        sellCount: 0,
        buyError: 0,
        sellError: 0,
        assets: parseFloat(assetsAll),
        assetsNum0: assetsAll / klineData[0].list[0].close,
        assetsNum1: assetsAll / klineData[1].list[0].close,
      };

      klineData[0].list.forEach((item, index) => {
        this.calculateDouble3(
          item,
          "assetsNum0",
          info,
          assetsAll,
          assetsSplit,
          assetsScale,
          riseKey,
          gas
        );
      });

      // klineData[1].list.forEach((item, index) => {
      //   this.calculateDouble3(
      //     item,
      //     "assetsNum1",
      //     info,
      //     assetsAll,
      //     assetsSplit,
      //     assetsScale,
      //     riseKey,
      //     gas
      //   );
      // });

      let data = {
        currency: klineData[0].currency + "," + klineData[1].currency,
        period: klineData[0].period,
        length: klineData[0].list.length,
        assets0: _N(
          info.assetsNum0 *
            klineData[0].list[klineData[0].list.length - 1].close,
          2
        ),
        assets1: _N(
          info.assetsNum1 *
            klineData[1].list[klineData[1].list.length - 1].close,
          2
        ),
        assetsAll,
        assetsSplit,
        assetsScale,
        riseKey,
        gas,
        ...info,
        time: _D(),
        assets: _N(info.assets, 2),
      };
      data.change = _N(
        data.assets + data.assets0 + data.assets1 - 3 * assetsAll,
        2
      );
      let day =
        (klineData[0].list[klineData[0].list.length - 1].id -
          klineData[0].list[0].id) /
        (24 * 60 * 60);
      // Log("day", day);
      data.rate = data.change / (parseFloat(assetsAll) * 3) / day;
      // / ((klineData[0].list[klineData[0].list.length - 1].id - klineData[0].list[0].id) / 24 * 60 * 60);
      return data;
    },
    calculateDouble3(
      item,
      assetsNumkey,
      info,
      assetsAll,
      assetsSplit,
      assetsScale,
      riseKey,
      gas
    ) {
      let assetsSplitMax = assetsSplit * assetsScale;
      let balance2usdt = info[assetsNumkey] * item.close;

      let rise = (item.close - item.open) / item.open;
      if (Math.abs(rise) > parseFloat(riseKey)) {
        if (rise > 0) {
          // 
          let assetsSplitS =
            balance2usdt > info.assets ? assetsSplitMax : assetsSplit;
          let num = parseFloat(assetsSplitS) / item.close;
          if (info[assetsNumkey] > num) {
            info.assets += parseFloat(assetsSplitS) * (1 - parseFloat(gas));
            info[assetsNumkey] -= parseFloat(assetsSplitS) / item.close;
            info.sellCount++;
          } else {
            info.sellError++;
            // Log('')
          }
        } else {
          // 
          let assetsSplitS =
            balance2usdt > info.assets ? assetsSplit : assetsSplitMax;
          if (info.assets > assetsSplitS) {
            info.assets -= assetsSplitS;
            info[assetsNumkey] +=
              (assetsSplitS / item.close) * (1 - parseFloat(gas));
            info.buyCount++;
          } else {
            info.buyError++;
            // Log('')
          }
        }
      }
    },
  },
};

function GetRecords(currency, period, config2, url) {
  let size = config2.size;
  let params = {
    symbol: (currency + "usdt").toLowerCase(),
    period,
    size,
  };
  return new Promise((reslove, reject) => {
    axios({
      method: "get",
      url,
      params,
    })
      .then((res) => {
        if (res.data.status != "error") {
          let records = res.data.data;
          reslove(records);
        } else {
          Log(":GetRecords1", res);
          reslove([]);
        }
      })
      .catch((res) => {
        Log(":GetRecords", res);
        reslove([]);
      });
  });
}

function GetRecords2(currency, period, config2, url, dates) {
  let size = config2.size;
  let params = {
    currency,
    period,
    size,
    dates: JSON.stringify(dates),
  };
  return new Promise((reslove, reject) => {
    axios({
      method: "get",
      url,
      params,
    })
      .then((res) => {
        reslove(res.data);
      })
      .catch((res) => {
        Log(":GetRecords", res);
        reslove([]);
      });
  });
}
function _D(time) {
  return require("dayjs")(time).format("YYYY-MM-DD HH:mm:ss");
}

function _N(time, num = 0) {
  return Math.floor(time * Math.pow(10, num)) / Math.pow(10, num);
}

function _Round(time, num = 0) {
  return Math.round(time * Math.pow(10, num)) / Math.pow(10, num);
}

function Log() {
  console.log(_D(), ...arguments);
}
function Uint8ArrayToString(fileData) {
  var dataString = "";
  for (var i = 0; i < fileData.length; i++) {
    dataString += String.fromCharCode(fileData[i]);
  }
  return dataString;
}
</script>


