<template>
  <div class="home">
    <div class="inputArea">
      <div class="item">
        <div class="title"></div>
        <el-input
          v-model="config3.currencys"
          placeholder=""
        ></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="config3.period" placeholder=""></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-input v-model="config3.size" placeholder=""></el-input>
      </div>
      <div class="item">
        <div class="title"></div>
        <el-date-picker
          v-model="config3.dates"
          type="datetimerange"
          align="right"
          start-placeholder=""
          end-placeholder=""
          :default-time="['00:00:00', '00:00:00']"
          @change="getData3"
        >
        </el-date-picker>
      </div>
      <div class="item">
        <div class="title">ws</div>
        <el-input v-model="config3.timeGap" placeholder=""></el-input>
      </div>
      <div
        class="item"
        v-for="(item, index) in riseShowData"
        :key="index + Math.random()"
      >
        <div>{{ item.period }}</div>
        <div style="margin-left: 10px">
          {{ item.currency0 + "-" + item.currency1 }}
        </div>
        <div style="margin-left: 10px">
          {{ item.tick0.close + "-" + item.tick1.close }}
        </div>
        <div style="margin-left: 10px">
          {{ item.rise0 + "-" + item.rise1 }}
        </div>
        <div style="margin-left: 10px">
          {{ item.rise }}
        </div>
      </div>
    </div>
    <div class="buttons">
      <el-button
        type="primary"
        :loading="loading3"
        :disabled="disabled3"
        @click="daydayday(1)"
        ></el-button
      >
      <el-button
        type="primary"
        :loading="loading3"
        @click="getData3()"
        :disabled="disabled3"
        ></el-button
      >
      <el-button
        type="primary"
        :loading="loading3"
        :disabled="disabled3"
        @click="daydayday(2)"
        ></el-button
      >
      <el-button type="danger" @click="klineData = []"></el-button>
      <el-button
        type="primary"
        :loading="loading3"
        @click="calculateDouble1(klineData)"
        :disabled="disabled3"
        ></el-button
      >
    </div>
    <div class="data">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
      >
        <el-table-column
          prop="currency"
          label="currency"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="period"
          label="period"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="price"
          label="price"
          width="80"
        ></el-table-column>
        <el-table-column prop="length" label="length"> </el-table-column>

        <el-table-column
          prop="start"
          label="start"
          width="165"
        ></el-table-column>
        <el-table-column prop="end" label="end" width="165"> </el-table-column>

        <el-table-column fixed="right" label="" width="80">
          <template slot-scope="scope">
            <el-button
              type="danger"
              size="small"
              @click="klineData.splice(scope.$index, 1)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-divider></el-divider>
    <div id="main" style="height: 500px"></div>
  </div>
</template>

<script>
// @ is an alias to /src
var axios = require("axios");
import HelloWorld from "@/components/HelloWorld.vue";
import { mapState, mapMutations, mapGetters } from "vuex";
import CryptoJS from "crypto-js/crypto-js";
import * as echarts from "echarts";
export default {
  // name: "Home",
  components: {
    HelloWorld,
  },
  data() {
    return {
      input: "",
      loading: false,
      loading2: false,
      loading3: false,
      disabled3: true,
      ws: null,
      ccData: [],
      riseData: {},
      riseShowData: [],
      myChart: null,
      klineData: [],
    };
  },
  computed: {
    ...mapState({
      config3: (state) => state.config3,
      urls: (state) => state.urls,
      calculateData: (state) => state.calculateData,
      spotData: (state) =>
        state.accounts.filter((item) => item.type == "spot")[0] || {},
    }),
    currencys() {
      return this.config3.currencys.split(",");
    },
    periods() {
      return this.config3.period.split(",");
    },
    tableData() {
      return this.klineData.map((item) => {
        return {
          currency: item.currency,
          period: item.period,
          price: item.price,
          length: item.list.length,
          start: _D(item.list.length ? item.list[0].id * 1000 : 0),
          end: _D(
            item.list.length ? item.list[item.list.length - 1].id * 1000 : 0
          ),
          time: _D(),
        };
      });
    },
    option() {
      let item0 = this.ccData[0];
      return item0
        ? {
            title: {
              text: item0.currency0 + "-" + item0.currency1,
            },
            tooltip: {
              trigger: "axis",
              axisPointer: {
                type: "cross",
                label: {
                  backgroundColor: "#6a7985",
                },
              },
            },
            legend: {
              data: this.ccData.map(
                (item) => item.currency0 + "-" + item.currency1
              ),
            },
            toolbox: {
              feature: {
                saveAsImage: {},
              },
            },
            grid: {
              left: "3%",
              right: "4%",
              bottom: "3%",
              containLabel: true,
            },
            // xAxis: [
            //   {
            //     type: "category",
            //     boundaryGap: false,
            //     data: ["", "", "", "", "", "", ""],
            //   },
            // ],
            xAxis: this.ccData.map((item) => {
              return {
                type: "category",
                boundaryGap: false,
                data: item.rises.map((item) => item.time),
              };
            }),
            yAxis: [
              {
                type: "value",
              },
            ],
            // series: [
            //   {
            //     name: "",
            //     type: "line",
            //     stack: "",
            //     areaStyle: {},
            //     emphasis: {
            //       focus: "series",
            //     },
            //     data: [120, 132, 101, 134, 90, 230, 210],
            //   },
            // ],
            series: this.ccData.map((item) => {
              return {
                name: this.ccData.map(
                  (item) => item.currency0 + "-" + item.currency1
                ),
                type: "line",
                stack: "",
                areaStyle: {},
                emphasis: {
                  focus: "series",
                },
                data: item.rises.map((item) => item.rise),
              };
            }),
            dataZoom: [
              {
                show: true,
                realtime: true,
                start: 0,
                end: 100,
              },
              {
                type: "inside",
                realtime: true,
                start: 0,
                end: 50,
              },
            ],
          }
        : {};
    },
  },
  created() {
    let ws = new WebSocket("wss://api.huobi.pro/ws");
    this.ws = ws;
    ws.onopen = (evt) => {
      //
      Log("Connection open ...");
      this.disabled3 = false;
    };
    ws.onmessage = (evt) => {
      //
      // console.log("Received Message: " + evt.data);
      var content = evt.data; //
      var reader = new FileReader();
      reader.readAsArrayBuffer(content);
      let that = this;
      reader.onload = () => {
        content = reader.result;
        const pako = require("pako");
        const restored = pako.inflate(content);
        let str = Uint8ArrayToString(restored);
        // Log("WS Received Message: " + str);
        let data = JSON.parse(str);
        if (data.ping) {
          let pong = JSON.stringify({
            pong: data.ping,
          });
          ws.send(pong);
        } else {
          if ((data.id = "111")) {
            this.getWSData1(data);
          }
          if ((data.id = "222")) {
            this.getWSData2(data);
          }
        }
        if (data.status == "error") {
          this.$message.error(data["err-msg"]);
          this.loading3 = false;
        }
      };
    };

    ws.onclose = (evt) => {
      //
      Log("Connection closed.");
      this.disabled3 = true;
    };
  },
  mounted() {
    var myChart = echarts.init(document.getElementById("main"));
    this.myChart = myChart;
    myChart.setOption(this.option);
  },
  destroyed() {
    this.ws.close();
  },
  methods: {
    getWSData1(data) {
      if (data && data.status == "ok") {
        data = data.data;
        if (this.wsParams.index < this.klineData.length) {
          this.klineData[this.wsParams.index].list = [
            ...this.klineData[this.wsParams.index].list,
            ...data,
          ];
          this.wsParams.arr[this.wsParams.index].index++;
        } else {
          let item = this.wsParams.arr[this.wsParams.index];
          this.klineData.push({
            currency: item.currency,
            period: item.period,
            list: data,
            price: "-",
          });
          this.wsParams.arr[this.wsParams.index].index++;
        }
        let item = this.wsParams.arr[this.wsParams.index];
        if (item.index >= item.list.length) {
          this.wsParams.index++;
        }
        if (
          this.wsParams.index >= this.klineData.length &&
          item.index >= item.length
        ) {
          this.loading3 = false;
          return;
        }
        setTimeout(() => {
          this.queryKLineFromWS();
        }, this.config3.timeGap);
      } else {
        // Log("getWSData1", JSON.stringify(data));
      }
    },
    getWSData2(data) {
      if (data.ch) {
        this.riseData[data.ch] = data.tick;
        // Log("riseData", this.riseData);

        // Log("riseShowData");
        let arr = [];
        let periods = this.periods;
        for (const index in periods) {
          let period = periods[index];
          let currency0 = this.currencys[0];
          let currency1 = this.currencys[1];
          let tick0 =
            this.riseData[`market.${currency0}usdt.kline.${period}`] || {};
          let tick1 =
            this.riseData[`market.${currency1}usdt.kline.${period}`] || {};
          let item = {
            period,
            currency0,
            currency1,
            tick0,
            tick1,
            rise0: (tick0.close - tick0.open) / tick0.open,
            rise1: (tick1.close - tick1.open) / tick1.open,
          };
          item.rise = _N(item.rise0 + item.rise1, 4);
          item.rise0 = _N(item.rise0, 4);
          item.rise1 = _N(item.rise1, 4);
          arr.push(item);
        }
        this.riseShowData = arr;
      }
    },
    queryKLineFromWS() {
      // this.wsParams
      if (this.wsParams.index < this.wsParams.arr.length) {
        let item = this.wsParams.arr[this.wsParams.index];
        let currency = item.currency;
        let period = item.period;
        let item2 = item.list[item.index];
        let from = item2.from;
        let to = item2.to;
        Log(currency, period, _D(from * 1000), _D(to * 1000));
        this.ws.send(
          JSON.stringify({
            req: `market.${currency}usdt.kline.${period}`,
            to,
            from,
            id: "111",
          })
        );
        return;
      } else {
        Log("queryKLineFromWS end");
        this.loading3 = false;
        this.calculateDouble1(this.klineData);
      }
    },
    daydayday(type) {
      let m = 24 * 60 * 60 * 1000 * (type == 1 ? -1 : 1);
      let dates = this.config3.dates;
      this.config3.dates = [
        new Date(new Date(dates[0]).getTime() + m),
        new Date(new Date(dates[1]).getTime() + m),
      ];
      this.getData3()
    },
    getData3() {
      this.loading3 = true;
      this.klineData = [];
      let currencys = this.currencys;
      let periods = this.periods;
      let size = parseInt(this.config3.size);
      let date0 = new Date(this.config3.dates[0]);
      let date1 = new Date(this.config3.dates[1]);
      let arr = [];
      currencys.forEach((currency) => {
        periods.forEach((period) => {
          let scale = 1;
          if (period.includes("hour")) {
            scale = 60;
          } else if (period.includes("day")) {
            scale = 60 * 24;
          } else if (period.includes("mon")) {
            scale = 60 * 24 * 30;
          }
          let m = parseInt(period) * scale;
          let gapM = m * size * 60;
          let time = date0.getTime() / 1000;
          let list = [];
          while (time < date1.getTime() / 1000) {
            let item = {
              from: time,
              to:
                time + gapM > date1.getTime() / 1000
                  ? date1.getTime() / 1000
                  : time + gapM,
            };
            item.fromT = _D(item.from * 1000);
            item.toT = _D(item.to * 1000);

            list.push(item);
            time += gapM + m * 60;
          }
          arr.push({
            currency,
            period,
            list,
            index: 0,
          });
        });
      });
      this.wsParams = {
        arr,
        index: 0,
      };
      this.queryKLineFromWS();
    },
    async calculateDouble1(klineData) {
      if (klineData.length == 2) {
        let list0 = klineData[0].list;
        let list1 = klineData[1].list;

        list0.some((item, key) => {
          if (list0[key].id == list1[key].id) {
          } else {
            this.$message.error("" + key);
            return true;
          }
        });
        this.loading2 = true;

        let data = [
          this.config3.assetsAll,
          this.config3.assetsSplit,
          this.config3.assetsScale,
          this.config3.riseKey,
          this.config3.gas,
        ].map((item) => {
          let list = [];
          let arr = String(item).split("_");
          if (arr.length > 1) {
            let arr2 = arr[1].split(".");
            let gap = 1;
            if (arr2.length > 1) {
              gap = Math.pow(10, -arr2[1].length);
            }
            let start = parseFloat(arr[0]);
            let newNum = start;
            list.push(start);
            while (parseFloat(list[list.length - 1]) < parseFloat(arr[1])) {
              newNum = _Round(
                list[list.length - 1] + gap,
                gap == 1 ? 0 : arr2[1].length
              );
              list.push(newNum);
            }
          } else {
            list = arr;
          }
          return list;
        });

        let list = [];
        data[0].forEach((item0) => {
          data[1].forEach((item1) => {
            data[2].forEach((item2) => {
              data[3].forEach((item3) => {
                data[4].forEach((item4) => {
                  list.push([item0, item1, item2, item3, item4]);
                });
              });
            });
          });
        });
        let arr = [];
        list.forEach((item) => {
          arr.push(
            this.calculateData2(
              klineData,
              parseFloat(item[0]),
              parseFloat(item[1]),
              parseFloat(item[2]),
              parseFloat(item[3]),
              parseFloat(item[4])
            )
          );
        });
        Log("", arr.length);
        this.loading2 = false;
        this.ccData = arr;
        this.myChart.setOption(this.option);
      } else {
        this.$message.error("");
      }
    },
    calculateData2(
      klineData,
      assetsAll = 100,
      assetsSplit = 10,
      assetsScale = -0.01,
      riseKey = 0.005,
      gas = 0.002
    ) {
      let list0 = klineData[0].list;
      let list1 = klineData[1].list;
      let info = {
        asset: 0,
        num0: 0,
        num1: 0,
        count: 0,
        item0: list0[0],
        item1: list1[0],
      };
      let rises = [];
      list0.some((item, index) => {
        let item0 = list0[index];
        let item1 = list1[index];

        let rise0 = (item0.high - info.item0.open) / info.item0.open;
        let rise1 = (item1.high - info.item1.open) / info.item1.open;
        let rise = rise0 + rise1;
        rises.push({
          time: _D(item.id * 1000),
          rise,
        });
      });
      let item = {
        currency0: klineData[0].currency,
        currency1: klineData[1].currency,
        rises,
      };
      return item;
    },
  },
};

function GetRecords(currency, period, config3, url) {
  let size = config3.size;
  let params = {
    symbol: (currency + "usdt").toLowerCase(),
    period,
    size,
  };
  return new Promise((reslove, reject) => {
    axios({
      method: "get",
      url,
      params,
    })
      .then((res) => {
        if (res.data.status != "error") {
          let records = res.data.data;
          reslove(records);
        } else {
          Log(":GetRecords1", res);
          reslove([]);
        }
      })
      .catch((res) => {
        Log(":GetRecords", res);
        reslove([]);
      });
  });
}

function GetRecords2(currency, period, config3, url, dates) {
  let size = config3.size;
  let params = {
    currency,
    period,
    size,
    dates: JSON.stringify(dates),
  };
  return new Promise((reslove, reject) => {
    axios({
      method: "get",
      url,
      params,
    })
      .then((res) => {
        reslove(res.data);
      })
      .catch((res) => {
        Log(":GetRecords", res);
        reslove([]);
      });
  });
}
function _D(time) {
  return require("dayjs")(time).format("YYYY-MM-DD HH:mm:ss");
}

function _N(time, num = 0) {
  return Math.floor(time * Math.pow(10, num)) / Math.pow(10, num);
}

function _Round(time, num = 0) {
  return Math.round(time * Math.pow(10, num)) / Math.pow(10, num);
}

function Log() {
  console.log(_D(), ...arguments);
}
function Uint8ArrayToString(fileData) {
  var dataString = "";
  for (var i = 0; i < fileData.length; i++) {
    dataString += String.fromCharCode(fileData[i]);
  }
  return dataString;
}
</script>


