<template>
  <div class="about">
    <h1>This is an about page</h1>
    <el-upload
      class="upload-demo"
      ref="upload"
      action="https://jsonplaceholder.typicode.com/posts/"
      :auto-upload="false"
      :on-change="fileChange"
      drag
    >
      <el-button slot="trigger" size="small" type="primary"></el-button>
    </el-upload>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  created() {
    // this.loadData();
  },
  computed: {
    ...mapState({
      config1: (state) => state.config1,
      urls: (state) => state.urls,
    }),
  },
  methods: {
    fileChange(file) {
      let url = "/v1/account/accounts/{account-id}/balance";
      console.log("fileChange", file);
      changeFile(file);
    },
    loadData() {
      let currencys = this.config1.currencys.split(",");
      let params = this.$util.huobiParams(
        {
          symbol: currencys[0] + "usdt",
          states: "filled",
          "end-time": new Date().getTime(),
          "start-time": new Date().getTime() - 0.1 * 24 * 60 * 60 * 1000,
        },
        this.urls.orders,
        "get"
      );
      console.log(params);
      return new Promise((reslove, reject) => {
        require("axios")({
          method: "get",
          url: this.urls.orders,
          params,
        })
          .then((res) => {
            if (res.data["err-msg"]) {
              console.log(res.data["err-msg"]);
            } else {
              this.$store.state.accounts = res.data.data;
              this.spotData = res.data.data.filter(
                (item) => item.type == "spot"
              )[0];
            }
          })
          .catch((res) => {
            Log(":testCLick", res);
            reslove([]);
          });
      });
    },
  },
};

function changeFile(file) {
  var reader = new FileReader();
  reader.readAsText(file.raw);
  reader.onload = function () {
    let content = this.result;
    var table = new Array();
    var rows = new Array();
    rows = content.split("\n");
    for (var i = 0; i < rows.length; i++) {
      // table.push(rows[i].split(","));
      if (i > 0) {
        let row0s = rows[0].split(",");
        let item = {};
        if (rows[i] && rows[i].length) {
          rows[i].split(",").forEach((element, index) => {
            if (index < row0s.length) {
              item[row0s[index].replace('"', "").replace('"', "")] = element;
            }
          });
          item. = parseFloat(item[""]);
          item. = item[""].includes("USDT")
            ? "USDT"
            : item..replace("/USDT", "");
          item.USDT =
            item. * (item. == "USDT" ? 1 : item.);
          table.push(item);
        }
      } else {
        // rows[0] += ',,USDT'
      }
    }

    let dataText = "";
    let keys = Object.keys(table[0]);

    keys.forEach((key, index) => {
      dataText += `${index ? "," : ""}${key}`;
    });

    table.forEach((item, index) => {
      dataText += "\n";
      keys.forEach((key, index) => {
        dataText += `${index ? "," : ""}${item[key]}`;
      });
    });

    let downLink = document.createElement("a");
    downLink.download = file.name;
    //blod
    let blob = new Blob([dataText]);
    downLink.href = URL.createObjectURL(blob);
    // 
    document.body.appendChild(downLink);
    downLink.click();
    // 
    document.body.removeChild(downLink);
  };
}

function _D(time) {
  return require("dayjs")(time).format("YYYY-MM-DD HH:mm:ss");
}

function _N(time, num = 0) {
  return Math.floor(time * Math.pow(10, num)) / Math.pow(10, num);
}
function Log() {
  console.log(_D(), ...arguments);
}
</script>

