<script>
const Config = {
  cacheKey: "CacheSP",
};
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  computed: {
    ...mapState({
      state: (state) => state,
    }),
  },
  watch: {
    state: {
      deep: true,
      handler(state) {
        this.saveData();
      },
    },
    $route(e) {
      //   console.log("$route", e);
      this.checkRoute(e);
    },
  },
  created() {
    this.loadData();
  },
  onHide: function () {},
  methods: {
    ...mapMutations({
      setState: "setState",
    }),
    saveData() {
      // console.log('localStorage updated');
      let data = {};
      this.$store.state.saveKeys.map((key) => {
        data[key] = this.$store.state[key];
      });
      localStorage[Config.cacheKey] = JSON.stringify(data);
      // uni.setStorage({
      //   key: Config.cacheKey,
      //   data: data,
      // });
    },
    async loadData() {
      try {
        const data = JSON.parse(localStorage[Config.cacheKey]);
        // const data = uni.getStorageSync(Config.cacheKey);
        if (data) {
          this.setState(data);
        } else {
          this.saveData();
        }
      } catch (e) {
        console.log("");
      }
    },
    checkRoute(e) {},
  },
};
</script>
